import React from "react";

const Register = ({ onRouteChange }) => {
  return (
    <article className="br3 ba  b--black-10 mv4 w-100 w-50-m w-25-l mw6 shadow-2 center">
      <main className="pa4 black-80">
        <div className="measure ">
          <fieldset id="sign_up" className="ba b--transparent ph0 mh0">
            <legend className="f1 fw6 ph0 mh0">Register</legend>
            <div className="mt3">
              <label className="db fw6 lh-copy f6" htmlFor="Name">
                Name
              </label>
              <input
                className="pa2 shadow-3 input-reset ba bg-transparent  hover-white w-100"
                type="text"
                name="name"
                id="name"
              />
            </div>
            <div className="mt3">
              <label className="db fw6 lh-copy f6" htmlFor="email-address">
                Email
              </label>
              <input
                className="pa2 shadow-3 input-reset ba bg-transparent  hover-white w-100"
                type="email"
                name="email-address"
                id="email-address"
              />
            </div>
            <div className="mv3">
              <label className="db fw6 lh-copy f6" htmlfor="password">
                Password
              </label>
              <input
                className="b pa2 shadow-3 input-reset ba bg-transparent  hover-white w-100"
                type="password"
                name="password"
                id="password"
              />
            </div>
          </fieldset>
          <div className="">
            <input
              onClick={() => onRouteChange("signin")}
              className="b ph3 pv2 input-reset br2 ba b--black bg-transparent grow pointer f6 dib "
              type="submit"
              value="register"
            />
          </div>
        </div>
      </main>
    </article>
  );
};
export default Register;
